@import 'assets/scss/utils/mixins';

.dealer-img {
	width: 55px;
	height: 40px;
	object-fit: contain;
	border-radius: 5px;
}

.ad-details {
	border: 5px solid var(--info-1);
	border-radius: 10px;

	@include media-breakpoint-down(sm) {
		border: 16px solid var(--info-1);
		border-radius: 0;
	}
}

.dealer-details {
	padding: 10px 16px;
	border-bottom: 1px solid var(--neutral-2);
}

.w-311px {
	min-width: 311px;
}

.ad-img {
	object-fit: cover;
	max-width: 100%;
	height: 100%;

	@include media-breakpoint-down(lg) {
		max-height: 250px;
		width: 100%;
	}
}

.selected-badge {
	border-radius: 5px;
	padding: 4.5px 16px;
	margin-inline-end: 8px;
	margin-bottom: 4px;
	display: inline-block;
	white-space: normal; /* Allow text to wrap */
	word-break: break-word;
	width: fit-content;
}

.highlight-arrow {
	display: inline-block !important;
	width: 18px;
	height: 18px;
	vertical-align: middle;
	font-size: 18px;
}

.highlight-btn {
	display: block;
	height: 40px;
	line-height: 40px;
	text-align: center;
}

.checkbox-item {
	border: 1px solid var(--general-3);
	height: 50px;
	border-radius: 4px;
}

.checkbox-list:nth-child(odd) {
	padding-inline-end: 16px;
}

.checked-badge {
	border: 1px solid var(--info-3);
	background-color: var(--info-1);
}

.detail-icon {
	display: inline-block !important;
	margin-inline-end: 4px;
	font-size: 16px;
	width: 16px;
	height: 16px;
	vertical-align: middle;
}

.ad-review-details {
	display: flex;
	flex-direction: column;
}

.image-1 {
	width: 100%;
	object-fit: cover;
}

.large-photo {
	flex: 1;

	@include media-breakpoint-up(sm) {
		width: 216px;
	}
}

.small-photos {
	width: 216px;
	height: 250px;

	@include media-breakpoint-down(sm) {
		height: 111px;
		width: 100%;
	}
}

.other-images {
	width: 100%;
	object-fit: cover;
	height: 121px;

	@include media-breakpoint-down(sm) {
		height: 100%;
	}
}

.image-container {
	height: 250px;

	@include media-breakpoint-down(sm) {
		height: 200px;
	}
}

.sub-image {
	height: 121px;

	@include media-breakpoint-down(sm) {
		height: 111px;
		width: 100%;
	}
}

.h-25px {
	height: 25px;
	line-height: 25px;
}

.strike-through {
	text-decoration: line-through;
}
